* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Work Sans', sans-serif;
}

:root {
  --theme-blue: #0083b3;
}

p {
  font-size: 16px;
}

a {
  text-decoration: none;
  color: black;
  font-size: 16px;
}

.main-container {
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  background-color: var(--background-color);
}

.emptyList-wrap {
  display: flex;
  justify-content: center;
}

.emptyList-wrap img {
  max-width: 500px;
  width: 100%;
}

.grid-2 {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 3rem;
}

.grid-3 {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 2rem;
}

.grid-4 {
  /* display: flex; */
  /* margin-top: 20px; */
  /* width: 100%; */
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 2rem;
  /* margin: 2rem; */

}

.grid-1-3 {
  display: grid;
  grid-template-columns: 25% auto;
  grid-gap: 3rem;
}

.grid-3-1 {
  display: grid;
  grid-template-columns: auto 25%;
  grid-gap: 3rem;
}

.grid-1-2 {
  display: grid;
  grid-template-columns: 33% auto;
  grid-gap: 3rem;
}

.section {
  margin: 10px 10vw;
}

.section-heading h1 {
  font-size: 40px;
  padding: 10px 0px;
  font-family: 'Maven Pro', sans-serif;
} 

.page-heading h1{
  color: #000000;
  font-size: 40px;
  font-weight: 800;
  position: relative;
  font-family: 'Maven Pro', sans-serif;
  margin-bottom: 60px;
}

.page-heading h1::before{
  content: "";
  position: absolute;
  left: 0;
  bottom: -15px;
  height: 4px;
  border-radius: 2px;
  width: 50px;
  background: rgb(58, 141, 235);
}






/* .................... */
/* Banner */
/* .................... */
#head {
  height: 70vh;
  max-height: 800px;
  width: 100%;
  display: flex;
  background-repeat: space;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-attachment: fixed;
  background-position: center;
  background-blend-mode: overlay;
  padding: 0 10vw;
  z-index: 0;

}

.banner-content {
  align-items: center;
  text-align: center;
  justify-content: center;
  margin: 20px;

}

.banner-content h1 {
  /* color: var(--theme-blue); */
  color: #e7f6f9;
  font-size: 74px;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.1);
  font-weight: 700;
  text-align: center;
  font-family: "Open sans", Helvetica, Arial;
}

.banner-content h2 {
  color: #ffffff;
  font-size: 24px;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.1);
  font-weight: 700;
  text-align: center;
  font-family: "Open sans", Helvetica, Arial;

}


.home-video {
  width: 100%;
  background: #151414;

}

.home-video video {
  width: 100vw;

}

.about-section {
  margin: 20px 0px;
}

.about-section p {
  font-size: 24px;
  font-family: 'Jost', sans-serif;

}


.team-section{
  margin-bottom: 40px;
}
.team-cards {
  text-align: center;
  border-radius: 50px;
  padding: 40px;
  background: #f3f2f2;
  position: relative;
  overflow: hidden;
  text-align: center;

}

.team-cards:before {
  position: absolute;
  content: '';
  left: 0px;
  bottom: 0px;
  width: 0%;
  height: 0%;
  border-radius: 50px;
  box-shadow: inset 0 0 25px rgba(0, 0, 0, 0.30);
  transition: all 0.4s ease;
  background-image: linear-gradient(to right, #3c70a4 0%, #64b2cd 100%);
}

.team-cards:hover:before {
  height: 100%;
  width: 100%;
}

.team-cards:hover .team-card-image{
  padding: 0;
}

.team-card-image {
  position: relative;
  max-width: 210px;
  max-height: 210px;
  margin: 0 auto;
  overflow: hidden;
  border-radius: 50%;
  padding: 15px;
  transition: all 0.5s ease;
  box-shadow: inset 0px 0px 20px rgba(0, 0, 0, 0.20);
}

.team-card-image img{
  border-radius: 50%;
  transition: all 500ms ease;
  width: 100%;
}

.team-card-desc {
  position: relative;
  color: white;
}




.team-card-desc  h2{
  color: rgb(0, 0, 0);
  font-size: 22px;
}

.team-card-desc  h3{
  color: rgb(0, 0, 0);
  font-size: 18px;
}
.team-cards:hover .team-card-desc  h3{
  color: rgb(255, 255, 255);
}
.team-cards:hover .team-card-desc  h2{
  color: rgb(255, 255, 255);
}

.card-item {
  text-align: center;

}


.card-item-image {
  width: 100%;
  overflow: hidden;
  border-radius: 10px;


}

.card-item-image img {
  width: 100%;
  border-radius: 10px;
  transition: transform 0.5s ease;
}

.card-item-image img:hover {
  transform: scale(1.1);
  /* box-shadow: 0 0 11px rgba(33,33,33,.5);  */
}






.recent-events-section {
  text-align: center;
}

.recent-events-section h2 {
  font-size: 36px;
}

.recent-events-item img {
  width: 100%;
}

.events-detalis img {
  width: 100%;
}





/* Contact */
.contact-us-form {
  margin-bottom: 20px;

}
.address-section h2 {
  color: #000000;
font-size: 30px;

}


.form-control {
  width: 100%;
  padding: 10px;
  margin: 5px 0px;
  font-size: 14px;

}

.submit-button {
  padding: 10px 15px;
  color: #000000;
  background-color: white;
  font-size: 16px;
  font-family: 'Maven Pro', sans-serif;
  margin-bottom: 10px;
  border: 2px solid rgb(58, 141, 235);
  border-radius: .25rem;
  transition: .3s;
}

.submit-button:hover {
  background-color: rgb(58, 141, 235);
  color: white;
  cursor: pointer;
}

.success-message {
  font-size: 16px;
  color: rgb(58, 141, 235);
  text-align: center;
  margin-bottom: 20px;
}









/* Sports Details Page */

.sports-details {}

.sports-details p {
  font-size: 18px;

}

.sports-details h2 {
  font-size: 30px;

}

.para-content {
  font-size: 18px;
}

.sports-page-img img {
  width: 100%;
  border-radius: 10px;
}

.sports-achievements {
  margin: 50px auto;
}

.sports-achievements h2 {
  font-size: 30px;
  text-align: center;
}


.sports-achievements-table {
  border-collapse: collapse;
  /* max-height: 500px; */
  /* overflow-y: scroll; */
  margin: auto auto;
  align-items: center;
  justify-content: center;

}

.sports-achievements-table td,
.sports-achievements-table th {
  border: 1px solid #ddd;
  padding: 10px;
}

.sports-achievements-table tr:nth-child(even) {
  background-color: #f2f2f2;
}

.sports-achievements-table tr:hover {
  background-color: #ddd;
}

.sports-achievements-table th {
  padding: 10px;
  text-align: left;
  background-color: #108de6;
  color: white;
}








.instagramContainer {
  margin: 50px 0px;
  text-align: center;
 
}



.instagram-media {
  width: 100%;
  height: 600px;
}










/* ......................... */
/* Footer */
/* ......................... */

.footer-section {
  background: #151414;
  position: relative;
}

.footer-container {
  margin: 0px 10vw;
}


.footer-content {
  position: relative;
  z-index: 2;
  padding: 50px 0px;

}


.footer-logo {
  /* margin-bottom: 30px; */
}

.footer-logo img {
  max-width: 100px;
}

.footer-text p {
  margin-bottom: 15px;
  font-size: 1rem;
  color: #7e7e7e;
  line-height: 28px;
}


.footer-widget-heading h3 {
  color: #fff;
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 40px;
  position: relative;
  font-family: 'Maven Pro', sans-serif;
}

.footer-widget-heading h3::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -15px;
  height: 2px;
  width: 50px;
  background: var(--theme-blue);
}

.footer-widget ul li {
  display: inline-block;
  width: 50%;
  margin-bottom: 12px;
}

.footer-widget ul li a:hover {
  color: var(--theme-blue);
}

.footer-widget ul li a {
  color: #878787;
  text-transform: capitalize;
}

.copyright-area {
  background: #202020;
  padding: 30px 10vw;


}

.copyright-text p {
  margin: 0;
  font-size: 14px;
  color: #878787;
}

.copyright-text p a {
  color: var(--theme-blue);
}

.footer-menu {
  text-align: right;

}

.footer-menu li {
  display: inline-block;
  margin-left: 20px;
  text-align: right;

}

.footer-menu li:hover a {
  color: var(--theme-blue);
}

.footer-menu li a {
  font-size: 14px;
  color: #878787;
}

.socialLinks {
  display: flex;
  width: 100%;
}

.socialLinks ul {
  display: flex;

}


.socialLinks ul li a i {
  margin: 5px;
  width: 40px;
  font-size: 24px;
  height: 40px;
  background: #fafafa;
  border-radius: 50%;
  text-align: center;
  line-height: 40px;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.socialLinks ul li a .fa-facebook-f {
  color: #4267b2;
}


.socialLinks ul li a .fa-instagram {
  color: #dd5044;
}

.socialLinks ul li a .fa-youtube {
  color: #dd5044;
}


.socialLinks ul li a:hover i {
  color: #fff;
}

.socialLinks ul li a:hover .fa-facebook-f {
  background: #4267b2;
  cursor: pointer;
}

.socialLinks ul li a:hover .fa-youtube {
  background: #f21d1d;
  cursor: pointer;
}

.socialLinks ul li a:hover .fa-instagram {
  background: #dd5044;
  cursor: pointer;
}




